import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {DisplayTasksComponent} from './modules/display-tasks/display-tasks.component';
import { DisplayTaskComponent } from './modules/display-task/display-task.component';
import { AddTaskComponent } from './shared/add-task/add-task.component';
import { UpdateTaskComponent } from './shared/update-task/update-task.component';


const routes: Routes = [
  {
    path: 'home',
    component: DisplayTasksComponent
  },
  {
    path: '',
    redirectTo: '/home',
    pathMatch: 'full'
  },
  { path: 'addTask',
    component: AddTaskComponent},
  { path: "updateTask/:id",
    component: UpdateTaskComponent},
  { path: ":id",
  component: DisplayTaskComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
